@import url(https://fonts.googleapis.com/css2?family=Knewave&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Carter+One&display=swap);
#root {
  font-family: "Noto Sans", sans-serif;
}

/***************** NAVIGATION BAR *****************/

.nav-style {
  display: flex;

  /* justify-content: flex-end; */
  background-color: #00FF00;
  width: 20%;
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  /* padding-left: -60px; */

}

.nav-style ul {
  /* display: flex; */
  flex-direction: column;
  /* justify-content: space-evenly; */
  margin-top: 1%;
}
.nav-style ul li {
  list-style: none;
}

.nav-style ul li a {
  text-decoration: none;
  font-weight: bold;
  color: black;
  font-size: 2em;
  font-weight: 800;
}







/***************** HOMEPAGE *****************/

.homepage {
}

.homepage-threads {
  display: flex;
  justify-content: center;
}

.homepage-header {
  text-align: center;
}

.map-container {
  display: flex;
  justify-content: center;
}
.mapbox {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  margin: 1%;
}

.abh-container{
  display:flex;
  justify-content: center;
}

.abh{
  width:200vh; 

}

/***************** THREADLIST *****************/

.threads {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: stretch;
}
.threadlist { 
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: stretch;
  margin: 1%;
}

.threadlist-results {
  width: 55%;
}

.threadlist-results a {
  color: black;
  text-decoration: none;
}
.threadlist-individual-result {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  padding: 1% 3% 1% 3%;
  margin: 2%;
}

.threadlist-individual-result h3 {
  font-family: 'Carter One', cursive;
  font-size: 2em;
}

.threadlist-individual-result a h3:hover { 
  color: black;
  text-decoration: none;
}
 

.threadlist-individual-result a h5:hover{
  text-decoration: none;
  color:black;
}

.threadlist-individual-result:hover{
  background-color: #F7BD8D;
}

.threadlist-individual-result a h5 {
  border-bottom: 2px solid currentColor;
  display: inline-block;
  line-height: 1.85;
  color: #6a6a6a; 
  font-size: 1rem;
}

.threadlist-buttons {
  display: flex;
}

.threadlist-buttons p { 

  border-bottom: 2px solid currentColor;
  display: inline-block;
  line-height: 1.85;
  color: #6a6a6a; 
  font-size: 1rem;
}

.threadlist-buttons p:hover { 
  color: black
}
 
/***************** ADD NEW THREAD *****************/

.add-thread {
  margin: 1%;
  width: 50%;
}

.add-thread h2 {

  font-family: 'Carter One', cursive;
  margin-bottom: 3%;
}

.thread-form {
  width: 100%; 
  border: 1px solid blacK;
  border-radius: 25px;

  padding: 3%;
}

.thread-form input[type="text"] {
  border: none;
  width: 90%;
  padding: 3%;
  border-radius: 10px;
  margin: 1%;
  font-size: 1.2rem;
}

.thread-form textarea {
  border: none;
  resize: none;
  margin: 1%;
  width: 90%;
  padding: 3%;
  border-radius: 10px;
  font-size: 1rem;
}

.thread-form-button {
  margin: 1%;
  border: none;
  width: 20%;
  height: 40px;
  font-size: 1rem;
  border-radius: 10px;
}

.thread-form-button:hover {
  background-color: #00FF00;
}

/***************** THREAD DETAILS *****************/

.thread-details {
  background-color: lightblue;
  width: 100%;
  height: 100vh;
  margin: 1%;
  display: flex;
  justify-content: center;
}

.thread-details-text {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: stretch;
  width: 55%;
  height: auto;

  margin: 5% 0%;
  background-color: rgba(255, 255, 255, 0.6);
  padding: 3%;
}

.thread-details-buttons {
  display: flex;
}

.thread-details-buttons p {
  margin-right: 10px;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  color: black;
}

.thread-details-buttons a {
  text-decoration: none;
}
.thread-details-edit-delete-buttons button {
  margin-right: 10px;
  padding: 10px 10px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  color: black;
  border: none;
}

.edit-thread {
  width: 40vw;
}

.edit-thread input[type="text"] {
  border: none;
  width: 100%;
  padding: 3%;
  border-radius: 10px;
  font-size: 1.2rem;
}

.edit-thread input[type="textarea"] {
  border: none;
  width: 100%;
  padding: 3%;
  border-radius: 10px;
  font-size: 1rem;
}

.edit-thread button {
  margin-right: 10px;
  padding: 10px 10px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  color: black;
  border: none;
}

.edit-thread-buttons {
  display: flex;
  justify-content: center;
}

/***************** WELCOME PAGE *****************/

.welcome-page {
  display: block;
  justify-content: center;
  margin-left: 25%;
  margin-top: 10%;
}

.welcome-picture {
 
}

.welcome-picture img { 
  width: 150vh;
  height: auto; 
  margin-top: -25%;
  margin-left: -15%;
  margin-bottom: -10%;

} 
.welcome-text {  
  display: block;
  align-self: center;
  width: 70%;
  margin-top: -11%;
  margin-left: -4%;
}


.welcome-text h2 {    

  color: rgb(170, 170, 170); 
}

.welcome-signup {
 margin-top: -2%;
}

.welcome-signup ul {
  display: flex;
  color: black;
  list-style: none;
  margin-left: -10%;
}

.welcome-signup ul li {
  font-size: 1.2em;
  display: flex;
  justify-content: space-evenly;
  width: 30%;
  padding: 2%;
  margin-left:-7%;
}

.welcome-signup ul li a {
  
  border-bottom: 6px solid currentColor;
  font-weight: 800;
  line-height: 1.85;
  color: rgb(170, 170, 170); 
  font-size: 1rem;
}

.welcome-signup ul li a:hover {
  background-color: #F7BD8D;
  text-decoration: none;
}

/***************** SIGN UP *****************/

.signup-page {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5%;
  margin-left: -5%;

}
.signup-text {
  margin-top: -20%;
}

.signup-text p {
  margin-top: 30%;
  margin-right: 10%;
  text-align: right;
}

.signup-form {
  width: 30%;
}

.signup-form h2{
  font-size: 10em;
  
  font-family: 'Carter One', cursive;
}

.signup-form-form{
  border: 1px solid black;
  padding: 5%;
  border-radius: 25px;
}

.signup-buttons {
  display: flex;
  justify-content: center;
}

.signup-buttons button {
  margin-right: 5%;
}

.tower-icon{
width: 35vh;
margin-left:5%;

}

/***************** LOG IN  PAGE *****************/

.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-form {
  width: 30%;
  margin-top: 10%;
}
.login-form h2 { 
  font-family: 'Carter One', cursive;
  font-size: 11rem;
  margin-bottom: 3%;
}

.login-form-form{
  border: 1px solid black;
  padding: 5%;
  border-radius: 25px;
}

.login-form-form input[type="text"] { 
  border:none;
}

.login-form-form input[type="password"] { 
  border:none;
}

.login-buttons {
  display: flex;
}

.login-buttons button {
  margin-right: 5%;
  background-color: white;
  border: none;
  border-bottom: 6px solid currentColor;
  font-weight: 800;
  line-height: 1.85;
  color: rgb(170, 170, 170); 
  font-size: 1rem;
}
.login-buttons button:hover {
   
  background-color: #F7BD8D;

}

.login-buttons a {
  margin-right: 5%;
  background-color: white;
  border: none;
  border-bottom: 6px solid currentColor;
  font-weight: 800;
  line-height: 1.85;
  color: rgb(170, 170, 170); 
  font-size: 1rem;
}

.login-buttons a:hover {
  margin-right: 5%;
  background-color: white;
  border: none;
  border-bottom: 6px solid currentColor;
  font-weight: 800;
  line-height: 1.85;
  color: rgb(170, 170, 170); 
  font-size: 1rem;
}

.login-buttons a:hover {
  margin-right: 5%;
  background-color: #F7BD8D;
   border-bottom: 6px solid currentColor;
  font-weight: 800;
  line-height: 1.85;
  color: rgb(170, 170, 170); 
  font-size: 1rem; border: none;

}

.login-buttons button:hover {
  margin-right: 5%;
  background-color: #F7BD8D;
  border: none;
  border-bottom: 6px solid currentColor;
  font-weight: 800;
  line-height: 1.85;
  color: rgb(170, 170, 170); 
  font-size: 1rem;
}

.bear-icon {
  width: 45vh; 
  margin-top: 5%;
}


/***************** LOG IN  PAGE *****************/

.profile-page {
  display: flex;
  justify-content: center;
  margin-top: 10%;
}
.profile-page ul {
  margin-top: 5%;
}

.profile-page ul li {
  list-style: none;
}

.profile-info {
  background-color: lightblue;
  width: 50%;
  display: block;
  justify-content: center;
}

 
.user-details-edit-delete-buttons {
  display: flex;
  justify-content: center;
  margin: 1%;
}

.user-details-edit-delete-buttons button {
  margin-right: 10px;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  border: none;
}
 
.edit-user-buttons button {
  margin-right: 10px;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  border: none;
}

.edit-user-form {
  width: 100%;
}

.edit-user-container {
  display: flex; 
  justify-content: center;
  padding-bottom: 5%;
}

